import { CCardHeader, CCard, CCardBody, CButton} from '@coreui/react'
import {React} from 'react'

const DataKirim = () =>{
    return(
        <>
        <div className="px-3 py-3">
        <CCard>
            <CCardHeader
            style={{backgroundColor:'#f9f9f9'}}
            >CHECKPOINT KELENGKAPAN DATA</CCardHeader>
            <CCardBody>
                <div className="d-flex flex-column justify-content-center">
                    <div className="d-flex flex-row justify-content-start card px-3 py-3">
                        <div className="mr-2" ><i style={{color:'green'}} class="fas fa-check-circle fa-lg"></i></div>
                        <div>Data Agen</div>
                    </div>
                    <div className="d-flex flex-row justify-content-start card px-3 py-3">
                        <div className="mr-2"><i style={{color:'green'}} class="fas fa-check-circle fa-lg"></i></div>
                        <div>Data Kapal</div>
                    </div>
                    <div className="d-flex flex-row justify-content-start card px-3 py-3">
                        <div className="mr-2"><i style={{color:'green'}} class="fas fa-check-circle fa-lg"></i></div>
                        <div>Data Barang</div>
                    </div>                                                          
                </div>
            </CCardBody>
        </CCard>  
        <CCard>
            <CCardHeader
            style={{backgroundColor:'#f9f9f9'}}
            >DISCLAIMER</CCardHeader>
            <CCardBody>
                <div className="d-flex flex-row">
                    <div className="mr-2">
                        <input type="checkbox"></input>
                    </div>
                    <div>
                        <a>Saya dengan ini menyatakan bahwa data yang saya sampaikan dalam pengajuan ini adalah benar dan saya bertanggung jawab apabila terjadi kesalahan di kemudian hari.</a>
                    </div>                    
                </div>
            </CCardBody>
        </CCard> 
        <div className="d-flex flex-row justify-content-end">
            <div className="mr-2"><CButton className="btn btn-secondary">Kembali</CButton></div>
            <div><CButton className="btn btn-light" style={{backgroundColor:'#02275d', color:'white'}}>Kirim</CButton></div>
        </div>                   
        </div>
        </>
    )
}

export default DataKirim